import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from './../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PizzaService {

  constructor(private http: HttpClient) { }
  public NumberOfItemsInCart: number = 0;
  getProducts() {
    return this.http.get(`${environment.url}/Products`);
  }

  postCart(product: any) {
    return this.http.post(`${environment.url}/Cart`, product);
  }

  getCart() {
    return this.http.get(`${environment.url}/Cart`);
  }

  removeItem(id: number) {
    return this.http.delete(`${environment.url}/Cart/${id}`);
  }
}
