import { Component } from '@angular/core';
import { PizzaService } from './services/pizza.service';
import { Cart } from "../app/models/cart";
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'pizza-app';

  constructor(public service: PizzaService) { }
  public Products: any;
  public cart = new Array<Cart>();
  public cartItem = new Cart();
  public Total: number = 0;
  public CartEmpty: boolean = true;

  ngOnInit() {
    this.service.getProducts().subscribe(data => {
      this.Products = data;
    });
    this.GetCart();
  }

  GetCart() {
    this.Total = 0;
    this.service.getCart().subscribe((data: Array<Cart>) => {
      this.cart = data;
      this.cart.forEach(element => {
        this.Total = this.Total + element.Price;
      });
      if (this.cart.length > 0) {
        this.CartEmpty = false;
      }
      this.service.NumberOfItemsInCart = this.cart.length;
    });
  }

  AddToCart(p: any) {
    this.cartItem.Name = p.name;
    this.cartItem.Description = p.description;
    this.cartItem.Price = p.price;
    this.cartItem.Quantity = 1;
    this.cartItem.Total = p.price;
    this.service.postCart(this.cartItem).subscribe((data) => {
      this.GetCart();
      alert('Item added to cart !');
    });
  }

  RemoveItem(id: number, p: Cart) {
    this.service.removeItem(id).subscribe((data) => {
      this.cart.splice(this.cart.indexOf(p), 1);
      if (this.cart.length == 0) {
        this.CartEmpty = true;
      }
      this.Total = this.Total - p.Price;
      this.service.NumberOfItemsInCart = this.cart.length;
    });
  }

  plusQty(p: Cart) {
    if (p.Quantity < 10) {
      p.Quantity = p.Quantity + 1;
      p.Total = p.Price * p.Quantity;
      this.Total = this.Total + p.Price;
    }
  }

  minusQty(p: Cart) {
    if (p.Quantity != 1) {
      p.Quantity = p.Quantity - 1;
      p.Total = p.Price * p.Quantity;
      this.Total = this.Total - p.Price;
    }
  }
}
